// Put home & front page javascript here
import '../../scss/home/loader.scss';

if (module.hot) {
  module.hot.accept();
}

function initAdvertiseBlock() {
  const advertiseEl = $('#module-advertise');
  if (advertiseEl.length) {
    const el = advertiseEl.clone()
      .removeClass('hidden-sm')
      .addClass('visible-sm');
    const el2 = el.clone();
    const latestWrap = $('#latest');
    const popularWrap = $('#popular');

    if (latestWrap.length) {
      const latestEl = (3 < latestWrap.find('.post').length) ?
        latestWrap.find('.post').eq(2) : latestWrap.find('.post').last();
      latestEl.after(el);
    }

    if (popularWrap.length) {
      const popularEl = (3 < popularWrap.find('.post').length) ?
        popularWrap.find('.post').eq(2) : popularWrap.find('.post').last();
      popularEl.after(el2);
    }
  }
}

